<template>
    <div>
        <h4>Home page content</h4>
        <hr>
        <label><b>Edit home page content</b></label><br>
        <textarea width="200" heigth="200" v-model="homePageContent"></textarea>

        <h4>Themes</h4>
        <hr>
        <label><b>Edit home page content</b></label><br>
        <textarea width="200" heigth="200" v-model="homePageContent"></textarea>
    </div>
</template>

<script>
  export default {
    name: 'application-settings',
    computed: {
      homePageContent: {
        get () {
          return this.$store.state.$_settings.homePageContent
        },
        set (content) {
          this.$store.commit('$_settings/setHomePageContent', content)
        }
      }
    }
  }
</script>


