import Vue from 'vue'
import Router from 'vue-router'
/* Top menu items */
import Settings from '../modules/settings/Settings.vue'
import Account from '../modules/account/Account.vue'
import HomePage from '../modules/homepage/HomePage.vue'
/* Setting sub-routes */
import ApplicationSettings from '../modules/settings/_components/ApplicationSettings.vue'
import JobSettings from '../modules/settings/_components/JobSettings.vue'
import GroupSettings from '../modules/settings/_components/GroupSettings.vue'
import UserSettings from '../modules/settings/_components/UserSettings.vue'
import PermissionSettings from '../modules/settings/_components/PermissionSettings.vue'
/* Core apps + dynamic apps */
import DynamicApp from '../modules/dynamicapp/DynamicApp.vue'

const addDynamicRoutes = (routes) => {
  const dynamicRoutes = window.INITIAL_STATE.dynamicRoutes || []
  return dynamicRoutes.reduce((accumulator, route) => {
    accumulator.push({
      path: '/' + route.id,
      name: route.name,
      component: DynamicApp,
      props: route.props,
      meta: {
        id: route.id
      }
    })
    return accumulator
  }, routes)
}

const getRoutes = () => {
  const routes = [
    {
      path: '/',
      name: 'Home',
      component: HomePage,
      meta: {
        id: 'home'
      }
    },
    {
      path: '/account',
      name: 'Account',
      component: Account,
      meta: {
        id: 'account'
      }
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings,
      meta: {
        id: 'settings'
      },
      children: [
        {path: 'application', component: ApplicationSettings},
        {path: 'jobs', component: JobSettings},
        {path: 'groups', component: GroupSettings},
        {path: 'permissions', component: PermissionSettings},
        {path: 'users', component: UserSettings}
      ]
    }
  ]
  return addDynamicRoutes(routes)
}

Vue.use(Router)
export default new Router({
  // mode: 'history', FIXME Use history but catch 404 on server for nested routes
  routes: getRoutes()
})



