<template>
    <div class="application">
        <application-header></application-header>
        <div role="main" class="container">
            <div class="row">
                <div class="col-md-12">
                    <router-view/>
                </div>
            </div>
        </div>
        <application-footer></application-footer>
    </div>
</template>

<style>
    /* Small devices (landscape phones, 576px and up) */
    @media (min-width: 576px) {
        .container {
            max-width: 70%
        }
    }

    /* Medium devices (tablets, 768px and up) */
    @media (min-width: 768px) {
        .container {
            max-width: 70%
        }
    }

    /* Large devices (desktops, 992px and up) */
    @media (min-width: 992px) {
        .container {
            max-width: 70%
        }
    }

    /* Extra large devices (large desktops, 1200px and up) */
    @media (min-width: 1200px) {
        .container {
            max-width: 70%
        }
    }
</style>

<script>
  import ApplicationHeader from './_components/ApplicationHeader.vue'
  import ApplicationFooter from './_components/ApplicationFooter.vue'

  import store from './_store'

  export default {
    name: 'application',
    components: {
      ApplicationHeader,
      ApplicationFooter
    },
    created () {
      /* register module store */
      this.$store.registerModule('$_application', store)
    }
  }
</script>


