<template>
    <header class="application-header">

        <!-- settings, account, sign-in / sign-out, sign-up top menu -->
        <nav class="navbar navbar-expand navbar-light">
            <div class="navbar-collapse collapse">
                <router-link to="/" class="navbar-brand mr-auto text-center">
                    <img src="https://avatars1.githubusercontent.com/u/1688158?v=4&s=280" class="align-middle" width="42"
                         height="42"/>
                </router-link>

                <ul class="nav navbar-nav ml-auto w-100 justify-content-end">
                    <router-link class="nav-link" to="/settings/application">Settings</router-link>
                    <router-link class="nav-link" to="/account">Account</router-link>
                    <li class="nav-item">
                        <a class="nav-link" href="#">Sign out</a>
                    </li>
                </ul>
            </div>
        </nav>

        <!-- core + dynamic apps -->
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">

            <!-- FIXME: https://mpetroff.net/2015/03/bootstrap-navbar-without-jquery/ -->
            <button class="navbar-toggler" type="button" data-toggle="collapse"
                    data-target="#application-header-content">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="navbar-collapse collapse" id="application-header-content">
                <ul class="nav navbar-nav ml-auto w-100 center">
                    <router-link v-for="route in routes" v-if="!topRoutes.includes(route.meta.id)" class="nav-link" :to="route.path" :key="route.name">
                        <h5>{{ route.name }}</h5>
                    </router-link>
                </ul>
            </div>
        </nav>
    </header>
</template>

<style>
    .application-header {
        margin-bottom: 1rem;
    }
</style>

<script>
  export default {
    name: 'application-header',
    data () {
      return {
        topRoutes: ['settings', 'account'],
        routes: this.$router.options.routes
      }
    }
  }
</script>



