const state = {
  homePageContent: ''
}

const setHomePageContent = (state, content) => {
  state.homePageContent = content
}

export default {
  state: state,
  getters: {},
  mutations: {
    setHomePageContent
  },
  actions: {},
  namespaced: true,
  strict: true
}



